const model = {
  name: 'user',
  state: {
    role: 'user', // 
    lastTime: 0, // 
  },
  reducers: {
    /**
     * 
     */
    setUserDetail(state, payload) {
      return {
        ...state,
        ...payload,
      };
    },
  },
  selectors: {
    /**
     * 
     * @desc 155
     */
    isExpired() {
      const now = new Date().getTime();
      return rootState => (now - rootState.user.lastTime) >= 3300000;
    },
  },
};

export default model;
